.t-list {
  font: var(--td-font-body-medium);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  color: var(--td-text-color-primary);
  background: var(--td-bg-color-container);
}
@-moz-document url-prefix() {
  .t-list {
    scrollbar-color: var(--td-scrollbar-color) transparent;
    scrollbar-width: thin;
  }
}
.t-list::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.t-list::-webkit-scrollbar-thumb {
  border: 0px solid transparent;
  background-clip: content-box;
  background-color: var(--td-scrollbar-color);
  border-radius: 11px;
}
.t-list::-webkit-scrollbar-thumb:vertical:hover,
.t-list::-webkit-scrollbar-thumb:horizontal:hover {
  background-color: var(--td-scrollbar-hover-color);
}
.t-list__inner {
  list-style: none;
  padding: 0;
  margin: 0;
}
.t-list-item {
  font: var(--td-font-body-medium);
  padding: var(--td-comp-paddingTB-m) var(--td-comp-paddingLR-l);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}
.t-list-item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
}
.t-list-item-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.t-list-item__meta {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.t-list-item__meta-avatar {
  width: var(--td-comp-size-xxxl);
  height: var(--td-comp-size-xxxl);
  border-radius: calc(var(--td-comp-size-xxxl) / 2);
  overflow: hidden;
  background: var(--td-bg-color-page);
  margin-right: var(--td-comp-margin-l);
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.t-list-item__meta-avatar img {
  max-width: 100%;
  width: 100%;
  height: 100%;
}
.t-list-item__meta-title {
  padding: 0;
  font: var(--td-font-title-medium);
  margin: 0 0 var(--td-comp-margin-s);
  color: var(--td-text-color-primary);
}
.t-list-item__meta-description {
  margin-right: var(--td-comp-margin-xxl);
  color: var(--td-text-color-primary);
}
.t-list-item__action {
  list-style: none;
  padding: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.t-list-item__action > li {
  display: inline-block;
}
.t-list-item__action > li:not(:last-child) {
  margin-right: var(--td-comp-margin-l);
}
.t-list-item__action > li .t-icon {
  color: var(--td-text-color-secondary);
  font-size: var(--td-comp-size-xxxs);
}
.t-list-item__action > li .t-icon:hover {
  color: var(--td-text-color-link);
  cursor: pointer;
}
.t-list-item__action > li > a {
  text-decoration: none;
  color: var(--td-brand-color);
}
.t-list--split .t-list-item::after {
  background: var(--td-border-level-1-color);
}
.t-list--stripe .t-list-item:nth-child(even) {
  background: var(--td-bg-color-secondarycontainer);
}
.t-list--vertical-action .t-list-item {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.t-list.t-size-s .t-list-item {
  padding: var(--td-comp-paddingTB-s) var(--td-comp-paddingLR-m);
}
.t-list.t-size-l .t-list-item {
  padding: var(--td-comp-paddingTB-l) var(--td-comp-paddingLR-xl);
}
.t-list__header,
.t-list__footer {
  background: var(--td-bg-color-container);
  padding: var(--td-comp-paddingTB-m) var(--td-comp-paddingLR-l);
}
.t-list__load {
  background: var(--td-bg-color-container);
  text-align: center;
  line-height: var(--td-line-height-body-medium);
}
.t-list__load > div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.t-list__load:not(:empty) {
  padding: var(--td-comp-paddingTB-m) var(--td-comp-paddingLR-l);
}
.t-list__load .t-loading {
  font-size: var(--td-comp-size-xxxs);
  margin-right: var(--td-comp-margin-s);
}
.t-list__load .t-loading.t-is-load-more {
  cursor: pointer;
}
